import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';

/*
  Generated class for the S2tProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

// 2553
// 

/**
 * 
 * @const
 */
const S = new String('');

/**
 * 
 * @const
 */
const T = new String('');

@Injectable()
export class S2tProvider {

  constructor(public http: Http) {
  }

  /**
   * 
   * @param {String} str - 
   * @param {Boolean} toT - 
   * @returns {String} - 
   */
  tranStr(str: string, toT: boolean): string {
      var i;
      var letter;
      var code;
      var isChinese;
      var index;
      var src, des;
      var result = '';

      if (toT) {
          src = S;
          des = T;
      } else {
          src = T;
          des = S;
      }

      if (typeof str !== "string") {
          return str;
      }

      for (i = 0; i < str.length; i++) {
          letter = str.charAt(i);
          code = str.charCodeAt(i);

          // Unicode
          // :
          // [1] http://www.unicode.org
          // [2] http://zh.wikipedia.org/wiki/Unicode%E5%AD%97%E7%AC%A6%E5%88%97%E8%A1%A8
          // [3] http://xylonwang.iteye.com/blog/519552
          isChinese = (code > 0x3400 && code < 0x9FC3) || (code > 0xF900 && code < 0xFA6A);

          if (!isChinese) {
              result += letter;
              continue;
          }

          index = src.indexOf(letter);

          if (index !== -1) {
              result += des.charAt(index);
          } else {
              result += letter;
          }
      }

      return result;
  }

  /**
 * HTML Element
 * @param {Element} element - HTML Element
 * @param {String|Array} attr - /
 * @param {Boolean} toT - 
 */
  tranAttr(element, attr, toT) {
      var i, attrValue;

      if (attr instanceof Array) {
          for (i = 0; i < attr.length; i++) {
              this.tranAttr(element, attr[i], toT);
          }
      } else {
          attrValue = element.getAttribute(attr);

          if (attrValue !== "" && attrValue !== null) {
              element.setAttribute(attr, this.tranStr(attrValue, toT));
          }
      }
  }

  /**
   * HTML Element
   * @param {Element} element - HTML Element
   * @param {Boolean} toT - 
   */
  tranElement(element, toT) {
      var i;
      var childNodes;

      if (element.nodeType !== 1) {
          return;
      }

      childNodes = element.childNodes;

      for (i = 0; i < childNodes.length; i++) {
          var childNode = childNodes.item(i);

          // HTML Element
          if (childNode.nodeType === 1) {
              // 
              if ("|BR|HR|TEXTAREA|SCRIPT|OBJECT|EMBED|".indexOf("|" + childNode.tagName + "|") !== -1) {
                  continue;
              }

              this.tranAttr(childNode, ['title', 'data-original-title', 'alt', 'placeholder'], toT);

              // input 
              // textinput
              if (childNode.tagName === "INPUT"
                  && childNode.value !== ""
                  && childNode.type !== "text"
                  && childNode.type !== "hidden") {
                  childNode.value = this.tranStr(childNode.value, toT);
              }

              // 
              this.tranElement(childNode, toT);
          } else if (childNode.nodeType === 3) {  // 
              childNode.data = this.tranStr(childNode.data, toT);
          }
      }
  }

}



